.traffic-legend {
  font-family: "segoeui";
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0;
  padding: 0;
  border: none;
  border-collapse: collapse;
  width: 150px;
  text-align: center;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  z-index: 100;
  box-shadow: rgba(0, 0, 0, 0.16) 0 0 4px;
  background-color: white;
  color: black;
}
.hidden {
  visibility: hidden;
}
