.azure-maps-control-container.dark .traffic-legend {
  background-color: #011c2c;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 4px;
  color: white;
}
.traffic-legend table {
  border-collapse: collapse;
  padding: 0;
  margin: 4px;
  text-align: left;
  font-size: 12px;
}
